/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: "Alegreya Sans", sans-serif;
	line-height: 1.6;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	clear: both;
	font-family: "Playfair Display", serif;
	line-height: 1.3em;
}

h1 {
	font-size: 2rem;
}

@media screen and (min-width: 1600px) {
	h1 {
		font-size: 3rem;
	}
}

h2 {
	font-size: 1.5em;
}

@media screen and (min-width: 1024px) {
	h2 {
		font-size: 2rem;
	}
}

@media screen and (min-width: 1600px) {
	h2 {
		font-size: 3rem;
		line-height: 3rem;
	}
}

h3 {
	font-size: 1.5em;
}

h4 {
	font-size: 1.3em;
}

h5 {
	font-size: 1.1em;
}

.widget-title {
	background: unset;
	-webkit-background-clip: unset;
	-webkit-text-fill-color: unset;
}

.search-title {
	font-size: 1.2em;
}

p {
	margin-bottom: 1rem;
	font-family: "Alegreya Sans", sans-serif;
}

dfn,
cite,
em,
i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", courier, monospace;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code,
kbd,
tt,
var {
	font-family: monaco, consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 0.9375rem;
}

abbr,
acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark,
ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
/* Inherit box-sizing to more easily change it's value on a component level.
@link http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
	overflow-x: hidden;
}

body {
	background: white;
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul,
ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: 700;
}

dd {
	margin: 0 1.5em 1.5em;
}

img {
	height: auto;
	max-width: 100%;
	margin-top: 1.2em;
}

.wp-block-cover {
	max-width: initial;
	max-height: 300px;
}

figure {
	margin: 1em 0;
}

table {
	margin: 0 0 1.5em;
	width: 100%;
}

/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.site-header {
	z-index: 10000000;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	height: 70px;
	width: 100%;
	top: 0;
	background-color: white;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
	position: fixed;
}

@media screen and (min-width: 1024px) {
	.site-header {
		justify-content: space-around;
	}
}

@media screen and (min-width: 1024px) {
	.site-header {
		height: 100px;
		transition: all 0.2s ease-out;
	}
}

@media screen and (min-width: 1600px) {
	.site-header {
		justify-content: center;
	}
}

@media screen and (min-width: 1024px) {
	.smaller-header {
		height: 80px;
	}
}

.site-branding {
	display: flex;
	align-content: center;
	flex-wrap: wrap;
}

.site-description,
.site-title,
.main-navigation {
	margin: auto 1rem;
	font-size: 1rem;
}

.site-description {
	font-size: 0.7rem;
}

@media screen and (max-width: 380px) {
	.site-description {
		display: none;
	}
}

@media screen and (min-width: 768px) {
	.site-description {
		font-size: 0.9rem;
	}
}

.site-title a {
	font-size: 1.3rem;
	font-family: "Playfair Display", serif;
	font-weight: bold;
	background: #8A2387;
	/* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #F27121, #E94057, #8A2387);
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #F27121, #E94057, #8A2387);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

@media screen and (max-width: 380px) {
	.site-title a {
		font-size: 1rem;
	}
}

@media screen and (min-width: 1024px) {
	.site-title a {
		display: block;
		font-size: 2rem;
	}
}

@media screen and (min-width: 1600px) {
	.site-title a {
		transition: all 0.2s ease-out;
	}
}

@media screen and (min-width: 1600px) {
	.smaller-navbar a {
		font-size: 1.8rem;
	}
}

.custom-logo {
	height: 30px;
	width: 30px;
	margin: 5px;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #e41313 #ccc #bbb;
	border-radius: 3px;
	background: #e6e6e6;
	color: rgba(0, 0, 0, 0.8);
	font-size: 1rem;
	line-height: 1;
	padding: 0.4em;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	border-color: #ccc #bbb #aaa;
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #aaa #bbb #bbb;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
	padding: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

textarea {
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: #111;
	text-decoration: none;
	transition: all 0.2s ease-out;
}

a:visited {
	color: #111;
}

a:hover, a:focus, a:active {
	color: #E94057;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation,
.primary-menu {
	display: flex;
	justify-content: center;
}

.main-navigation ul,
.primary-menu ul {
	background-color: #E94057;
	color: white;
	margin: 0;
	padding-left: 0;
	list-style-type: none;
	letter-spacing: 0.1rem;
}

@media screen and (min-width: 1600px) {
	.main-navigation ul,
	.primary-menu ul {
		display: flex;
		flex-direction: row;
		height: unset;
		position: static;
		background-color: unset;
		justify-content: flex-end;
		min-width: 500px;
	}
}

.main-navigation ul li,
.primary-menu ul li {
	position: relative;
	padding: 1rem 0;
}

@media screen and (min-width: 1600px) {
	.main-navigation ul li:hover > a,
	.main-navigation ul li.focus > a,
	.primary-menu ul li:hover > a,
	.primary-menu ul li.focus > a {
		border-bottom: 1px solid #111;
		color: #111;
		border-bottom: 1px solid #111;
	}
}

.main-navigation ul li a,
.primary-menu ul li a {
	display: block;
	color: white;
}

@media screen and (min-width: 1600px) {
	.main-navigation ul li a,
	.primary-menu ul li a {
		box-sizing: border-box;
		color: black;
		border-bottom: 1px solid white;
		border-width: 1;
		padding: 0.4em 1em;
		border-width: 1px;
		transition: all 0.3s ease-in;
	}
}

.menu {
	position: absolute;
	left: 0px;
	top: 0px;
	display: flex;
	flex-direction: column;
	height: 100vh;
	width: 250px;
	background-color: #111;
	justify-content: center;
	align-items: center;
	transform: translateX(-350px);
	box-shadow: 0 1px 5px rgba(0, 0, 0, 0.5);
	transition: transform 0.2s ease-in;
}

@media screen and (min-width: 1600px) {
	.menu {
		box-shadow: unset;
		position: static;
		min-width: 350px;
		transition: unset;
		transform: translateX(0px);
		top: 0;
	}
}

.menu-active {
	display: flex;
	transform: translateX(0px);
}

@media screen and (min-width: 1600px) {
	.menu-active {
		position: unset;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {
	max-width: 900px;
	margin: 3em auto;
}

.comment-navigation .nav-links,
.posts-navigation .nav-links,
.post-navigation .nav-links {
	display: flex;
	margin: 0 auto;
	font-size: 1.2rem;
}

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
	flex: 1 0 50%;
}

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
	text-align: end;
	flex: 1 0 50%;
}

.toggler {
	padding: auto 10px;
	display: flex;
	flex-direction: column;
	margin: 5px 5px;
	cursor: pointer;
}

@media screen and (min-width: 1600px) {
	.toggler {
		display: none;
	}
}

.toggler div {
	height: 2px;
	width: 30px;
	margin: 4px 0;
	background-color: black;
}

.nav-menu {
	display: flex;
}

.sub-menu {
	display: none;
}

#social-menu {
	height: unset;
	width: unset;
	display: flex;
	flex-direction: row;
}

#social-menu li {
	list-style-type: none;
	margin: 1rem;
}

/*--------------------------------------------------------------
## Search form
--------------------------------------------------------------*/
.widget_search {
	display: flex;
	left: 0px;
	top: 0px;
	width: 100%;
	justify-content: center;
	align-content: center;
	list-style: none;
	height: 80px;
	margin: 0;
	padding-left: 0;
	position: fixed;
	width: 100vw;
	background-color: white;
	transform: translateY(-200px);
	opacity: 0;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
	transition: all 0.3s ease-in;
	z-index: 15000;
}

@media screen and (min-width: 1024px) {
	.widget_search {
		height: 100px;
	}
}

.search-active {
	transform: translateY(70px);
	opacity: 1;
}

@media screen and (min-width: 1024px) {
	.search-active {
		transform: translateY(100px);
	}
}

.search-active-scrolled {
	transform: translateY(70px);
	opacity: 1;
}

@media screen and (min-width: 1024px) {
	.search-active-scrolled {
		transform: translateY(80px);
	}
}

.search-form {
	margin: auto 0;
	display: flex;
	max-height: 2rem;
	justify-content: center;
}

.search-form label {
	height: 100%;
	display: flex;
	justify-content: center;
}

@media screen and (min-width: 1024px) {
	.search-form label {
		width: 500px;
	}
}

.search-field {
	font-size: 1em;
	height: 30px;
	font-family: "Alegreya Sans", sans-serif;
	flex-grow: 3;
	color: white;
	border-radius: 0;
	text-align: center;
}

.search-form input[type="submit"] {
	background: #8A2387;
	/* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #F27121, #E94057, #8A2387);
	/* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #F27121, #E94057, #8A2387);
	color: white;
	border-radius: 0;
	transition: all 0.1s ease-in;
}

@media screen and (min-width: 1024px) {
	.search-form input[type="submit"] {
		width: 200px;
	}
}

.search-form input[type="submit"]:hover {
	cursor: pointer;
	transform: scale(0.98);
}

.search-icon {
	margin-left: 30px;
	height: 35px;
	width: 35px;
	cursor: pointer;
	align-self: center;
}

.search-x {
	align-self: center;
	width: 25px;
	height: 25px;
	margin: 0 15px;
	cursor: pointer;
}

.cat-title {
	font-size: 1.6em;
}

/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/
.container {
	margin-top: 100px;
}

@media screen and (min-width: 1024px) {
	.container {
		padding: unset;
		margin: 0 auto;
	}
}

.site-container {
	margin: 5rem auto;
	padding: 0 1rem;
	max-width: 600px;
}

@media screen and (min-width: 1024px) {
	.site-container {
		margin: 130px auto;
		max-width: 600px;
	}
}

@media screen and (min-width: 1600px) {
	.site-container {
		margin: 120px auto;
		max-width: 700px;
	}
}

@media screen and (max-width: 1024px) {
	#primary {
		max-width: 600px;
		margin: 0 auto;
		margin-top: 60px;
	}
}

article {
	margin-top: 30px;
}

.excerpt {
	margin: 0 0.5em;
}

@media screen and (min-width: 1024px) {
	.excerpt {
		display: grid;
		grid-template-rows: 200px 200px;
		grid-template-columns: 400px 400px;
		align-content: center;
		justify-content: center;
		column-gap: 2rem;
		margin: 12rem auto;
	}
}

@media screen and (min-width: 1600px) {
	.excerpt {
		grid-template-rows: minmax(200px, 400px) 300px;
		grid-template-columns: minmax(400px, 700px) minmax(400px, 700px);
		margin: 10rem auto;
	}
}

.excerpt-header {
	border-left: 1px solid #111;
	padding-left: 1rem;
	margin-top: 1rem;
	z-index: 10000;
	background-color: rgba(255, 255, 255, 0.7);
	margin-bottom: 1rem;
}

@media screen and (min-width: 1024px) {
	.excerpt-header {
